<template>
    <div class="news-wrapper">
        <!-- 列表部分list组件，后面提出来 -->
        <div class="news-list">
            <template v-if="needTransition">
                <transition-group
                    appear
                    name="item"
                    @before-enter="beforeEnter"
                    @enter="enter"
                    tag="div">
                    <news-item v-for="(newsItem, i) in newsList"
                        :newsItem="newsItem"
                        :key="newsItem.nid"
                        :data-index="i">
                    </news-item>
                </transition-group>
            </template>
            <template v-else>
                <news-item v-for="(newsItem, i) in newsList"
                    :newsItem="newsItem"
                    :key="newsItem.nid"
                    :data-index="i">
                </news-item>
            </template>
        </div>
    </div>
</template>

<script>
import NewsItem from './NewsItem.vue';

export default {
    name: 'home',
    props: ['newsList', 'lastListLen', 'needTransition'],
    components: {
        NewsItem
    },
    data() {
        return {};
    },
    computed: {

    },
    methods: {
        beforeEnter(el) {
            el.style.opacity = 0;
            el.style.transform = 'translate(-0px, 50px)';
        },
        enter(el, done) {
            let delay = 100 * (el.dataset.index - this.lastListLen);
            setTimeout(() => el.style.transform = 'translate(0, 0)', delay);
            setTimeout(() => el.style.opacity = 1, delay / 4);
        }
    }
};
</script>

<style lang="stylus">
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom


a
    text-decoration none

</style>


